<template>
  <div id="contact">
    <h1>連絡我們</h1>
    <div class="description">
      <div class="description__decoration">
        <img src="~@/assets/gold_heart_icon.png" alt="" />
      </div>
      <div class="description__content">
        <p>
          請填寫所有資料，我們將會儘速與您聯絡 <br />
          也可以點擊以下使用Line或Facebook留言與我們聯絡。
        </p>
      </div>
      <ul class="description__links">
        <li class="description__links__item">
          <a href="http://line.me/ti/p/@goodafter.tw">
            <img src="~@/assets/line_logo.png" alt="" />
          </a>
        </li>
        <li class="description__links__item">
          <a href="http://m.me/goodafter.tw">
            <img src="~@/assets/facebook_logo.png" alt="" />
          </a>
        </li>
      </ul>
    </div>
    <b-form class="form" @submit.prevent="submit_form()">
      <b-form-group
        class="form__name"
        id="form__name"
        label-for="form__name__input"
      >
        <b-form-input
          class="form__name__input"
          id="form__name__input"
          v-model="form.name"
          type="text"
          required
          placeholder="姓名"
        ></b-form-input>
      </b-form-group>

      <b-form-group
      class="form__tel"
      id="form__tel"
      label-for="form__tel__input">
        <b-form-input
          class="form__tel__input"
          id="form__tel__input"
          v-model="form.tel"
          required
          type='tel'
          placeholder="電話"
        ></b-form-input>
      </b-form-group>

      <b-form-group
      class="form__mail"
      id="form__mail"
      label-for="form__mail__input">
        <b-form-input
          class="form__mail__input"
          id="form__mail__input"
          v-model="form.mail"
          required
          type='email'
          placeholder="E-mail"
        ></b-form-input>
      </b-form-group>

      <b-form-group
      class="form__message"
      id="form__message"
      label-for="form__message__textarea">
        <b-form-textarea
        class="form__message__textarea"
        id="form__message__textarea"
        v-model="form.message"
        placeholder="留言給我們..."
        rows="5"
        ></b-form-textarea>
      </b-form-group>

      <b-form-group
      class="form__captchaCode"
      id="form__captachaCode"
      label-for="form__captachaCode__input">
        <div class="form__captchaCode__img">
          <canvas class="canvas" width="75" height="40"></canvas>
        </div>
        <b-form-input
          class="form__captchaCode__input"
          id="form__captachaCode__input"
          v-model="form.captcha_code"
          required
          type='text'
        ></b-form-input>
        <a href="#" class="form__captchaCode__changeCode" @click.prevent='create_canvas()'>換一個</a>
      </b-form-group>

      <b-form-group
      class="form__submit"
      id="form__submit"
      label-for="form__submit__button">
        <button class="form__submit__button" type="submit" title="送出留言">
          <img src="~@/assets/submit_contact.png" alt="">
        </button>
      </b-form-group>

    </b-form>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      form: {
        name: '',
        tel: '',
        mail: '',
        message: '',
        captcha_code: '',
      },
      captcha_code: '',
    };
  },
  methods: {
    move_borderbottom() {
      // create an array with obj like
      // {
      //   path: '',
      //   left: '',
      //   width: '',
      // }
      const tempArray = [];
      const pathArray = this.$router.options.routes[2].children;
      const liArray = document.querySelectorAll('.header__nav__list__item');
      pathArray.forEach((element, index) => {
        tempArray[index] = {};
        tempArray[index].path = element.path;
        tempArray[index].left = liArray[index].offsetLeft;
        tempArray[index].width = $(liArray[index]).width();
      });
      // get current path
      const currentPath = this.$router.history.current.path;
      // find which path matches current path
      const pathIndex = tempArray.findIndex(
        (element) => `/front/${element.path}` === currentPath,
      );
      // get borderbottom initial left
      const headerNavWidth = $('.header__nav').width();
      const headerNavListWidth = $('.header__nav__list').width();
      const borderBottomInitialLeft = headerNavWidth - headerNavListWidth - 15;
      // rendor
      $('.header__nav__borderbottom').css({
        left: `${borderBottomInitialLeft + tempArray[pathIndex].left}px`,
        width: `${tempArray[pathIndex].width}px`,
      });
    },
    submit_form() {
      if (this.form.captcha_code === this.captcha_code) {
        alert('送出成功');
      } else {
        alert('驗證碼不正確');
        this.form.captcha_code = '';
        this.create_canvas();
      }
    },
    create_captacha_code() {
      const codelength = 4;
      const codebase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
      let code = '';
      for (let index = 0; index < codelength; index += 1) {
        const codebaseIndex = parseInt(Math.floor(Math.random() * codebase.length), 10);
        const codeAlphabet = codebase[codebaseIndex];
        code += codeAlphabet;
      }
      this.captcha_code = code;
    },
    create_canvas() {
      this.create_captacha_code();
      const canvas = document.querySelector('.canvas');

      // 宣告為2D繪圖
      const canvas2d = canvas.getContext('2d');

      // 繪製背景色
      canvas2d.fillStyle = 'white';
      canvas2d.fillRect(0, 0, 100, 100);

      // 繪製驗證碼
      canvas2d.font = 'bolder 25px Roboto';
      canvas2d.fillStyle = 'black';
      canvas2d.fillText(this.captcha_code, 5, 30);

      // 繪製干擾線
      canvas2d.strokeStyle = 'black';
      canvas2d.beginPath();
      canvas2d.lineWidth = '1';
      canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 0);
      canvas2d.lineTo(Math.floor(Math.random() * 60) + 5, 25);
      canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 10);
      canvas2d.lineTo(Math.floor(Math.random() * 70) + 5, 28);
      canvas2d.stroke();

      // 繪製干擾點
      canvas2d.strokeStyle = 'black';
      canvas2d.beginPath();
      canvas2d.lineWidth = '1';
      for (let i = 0; i < 100; i += 1) {
        const bex = Math.floor(Math.random() * 78);
        const bey = Math.floor(Math.random() * 28);
        const tex = parseFloat((Math.random() * 2).toFixed(2));
        const tey = parseFloat((Math.random() * 2).toFixed(2));
        const enx = bex + tex;
        const eny = bey + tey;
        canvas2d.moveTo(bex, bey);
        canvas2d.lineTo(enx, eny);
      }
      canvas2d.stroke();
    },
  },
  mounted() {
    this.move_borderbottom();
    this.create_canvas();
  },
};
</script>
